<template>
  <v-app>
    <sidebar v-bind:data="data_to_component"></sidebar>

    <keep-alive>
      <component v-bind:is="whichcomponent" v-bind:data="data_to_component"></component>
    </keep-alive>



  </v-app>
</template>

<script>
import sidebar from './components/sidebar/sidebar.vue'
import expense from './components/expense/add_expense.vue'
import home from './components/home/home.vue'
import search from './components/search/search.vue'
import budget from './components/budget/budjet.vue'
import account from './components/account/account.vue'
import report from './components/report/report.vue'
import fedback from './components/fedback/fedback.vue'
import about from './components/about/about.vue'


import { bus } from './main.js';

export default {
  name: 'App',

  components: {
    "sidebar":sidebar,
    "Home":home,
    "expense":expense,
    "Search":search,
    "Accounts":account,
    "Budget":budget,
    "Report":report,
    "Fedback":fedback,
    "About": about
  },

  data: () => ({
    token: localStorage.getItem('user-token') || '',
    data_to_component : {"loading_flag":true,
                          "initial_load_flag":true,
                          "logged_in_flag":false,
                          "expense_data":'',
                          "user_data":{},
                          "account_data":{},
                          "category":{},
                          "categoryaccount":{},
                          "subcategory":{},
                          "subcategoryaccount":{},
                          "profile_image":{},
                          "profile":{},
                          "buffer_data":[],

                          },
    whichcomponent : "Home",

  }),
  created: function(){
    console.log("[app.vue][created] Checking for jwt token ");
    if(this.token ==''){
      console.log("[app.vue][created] token is not there do login ");
      console.log("[app.vue][created] setting vue flag to be false in home top");
      console.log("[app.vue][created] data to home :::::: ");
      console.log(this.data_to_component);
      this.data_to_component.logged_in_flag = false
      this.whichcomponent = "Home";
    }
    else{
      console.log("[app.vue][created] token is there already logged in ");
      this.data_to_component.logged_in_flag = true;
      this.get_user_data();
      this.get_account_data();
      this.get_category_data();
      this.get_subcategory_data();
      this.get_account_category_data();
      this.get_account_subcategory_data();
      this.get_profile_image_data();
      this.get_profile_data();

      this.get_expense_data({'time_filter_selected':'Day','offset':null,'start_day':null,'end_day':null});

    }
    bus.$on('login', () => {
        this.token= localStorage.getItem('user-token') ;
        console.log("[app.vue][created][from bus][changecomponent] changing app vue data");
        this.data_to_component.logged_in_flag = true;
        this.get_user_data();
        this.get_account_data();
        this.get_category_data();
        this.get_subcategory_data();
        this.get_account_category_data();
        this.get_account_subcategory_data();
        this.get_profile_image_data();
        this.get_profile_data();
        this.get_expense_data({'time_filter_selected':'Day','offset':null,'start_day':null,'end_day':null});

      }
    );
    bus.$on('refresh', () => {
        console.log("[app.vue][created][from bus][refresh] refresh data");
        this.data_to_component.loading_flag = true
        // console.log(this.data_to_component);
        this.token= localStorage.getItem('user-token') ;
        this.get_user_data();
        this.get_account_data();
        this.get_category_data();
        this.get_subcategory_data();
        this.get_account_category_data();
        this.get_profile_data();
        this.get_account_subcategory_data();
        this.get_expense_data({'time_filter_selected':'Day','offset':null,'start_day':null,'end_day':null});
      }
    );
    bus.$on('changecomponent', (data) => {
      console.log("[app.vue][created][from bus][changecomponent] changing app vue component :",data);
      this.whichcomponent=data;
    },
    );
    bus.$on('delete_expense', (data) => {
      var baseURI = '/api/expense-tracker/expense/';
      console.log("[app.vue][created][from bus][delete_expense] deleting expense :",data);
      this.$http.delete(baseURI+data,{
        headers: {
          Authorization: 'JWT '+this.token
        }
      })
      .then(response => {
        console.log("[app][delete_expense] Deleting data ");
        console.log(response);

      })
      .catch(error => {
        console.log("[app][delete_expense] Error Deleting data");
        console.log(error);
      })

    },
    );
    bus.$on('delete_one_account', (data) => {
      var baseURI = '/api/expense-tracker/account/';
      console.log("[app.vue][created][from bus][delete_account] deleting account :",data);
      this.$http.delete(baseURI+data.id,{
        headers: {
          Authorization: 'JWT '+this.token
        }
      })
      .then(response => {
        console.log("[app][delete_account] Deleting account data ");
        console.log(response);
        console.log("[app][delete_account] geting account data ");
        this.get_account_data();
      })
      .catch(error => {
        console.log("[app][delete_account] Error Deleting account  data");
        console.log(error);
      })

    },
    );
    bus.$on('modify_account', (data) => {
      var baseURI = '/api/expense-tracker/account/';
      console.log("[app.vue][created][from bus][delete_expense] deleting expense :",data);
      var put_data = {
            "account_name": "",
            "balance": null,
            "profile": data.profile
        }
      var  i ;
      for (i in this.data_to_component.account_data){
        if (this.data_to_component.account_data[i].id ==data.account){
          put_data.account_name = this.data_to_component.account_data[i].account_name
          put_data.balance = this.data_to_component.account_data[i].balance

        }
      }
      if (data.type=="income"){
        put_data.balance  = put_data.balance - data.amount
      }
      if (data.type=="expense"){
        put_data.balance  = put_data.balance + data.amount
      }
      this.$http.put(baseURI+data.account+"/",put_data,{
        headers: {
          Authorization: 'JWT '+this.token
        }
      })
      .then(response => {
        console.log("[app][modify_account] Modifyed account data ");
        console.log(response);
        this.get_expense_data({'time_filter_selected':'Day','offset':null,'start_day':null,'end_day':null});
        this.get_account_data()

      })
      .catch(error => {
        console.log("[app][modify_account] Error Modifying account data ");
        console.log(error);
      })

    },
    );
    bus.$on('add_expense', (data) => {
      const baseURI = '/api/expense-tracker/expense/';
      console.log(data);
      console.log("[app.vue][created][from bus][add_expense] Adding data to db:",data);
      // this.data_to_component.buffer_data
      this.$http.post(baseURI,data,{
        headers: {
          Authorization: 'JWT '+this.token
        }
      })
      .then(response => {
        console.log("[app][add_expense] Added expense to home ");
        console.log(response);
        // localStorage.setItem("user-token",response.data.token);
        this.get_expense_data({'time_filter_selected':'Day','offset':null,'start_day':null,'end_day':null});
        this.get_account_data()


      })
      .catch(error => {
        console.log("[app][add_expense] error adding data to db");
        console.log(error);
      })

    },
    );
    bus.$on('transfer_amount', (data) => {
      const baseURI = '/api/expense-tracker/expensetransfer/';
      console.log(data);
      console.log("[app.vue][created][from bus][add_expense] Adding data to db:",data);
      // this.data_to_component.buffer_data
      this.$http.post(baseURI,data,{
        headers: {
          Authorization: 'JWT '+this.token
        }
      })
      .then(response => {
        console.log("[app][add_expense] Added expense to home ");
        console.log(response);
        // localStorage.setItem("user-token",response.data.token);
        this.get_account_data()
      })
      .catch(error => {
        console.log("[app][add_expense] error adding data to db");
        console.log(error);
      })

    },
    );
    bus.$on('add_account', (data) => {
      const baseURI = '/api/expense-tracker/account/';
      console.log(data);
      console.log("[app.vue][created][from bus][add_account] Adding new account to db:",data);
      // this.data_to_component.buffer_data
      this.$http.post(baseURI,data,{
        headers: {
          Authorization: 'JWT '+this.token
        }
      })
      .then(response => {
        console.log("[app][add_account] Added account to home ");
        console.log(response);
        console.log("[app][add_account] refreshing account to home ");
        this.get_account_data()

      })
      .catch(error => {
        console.log("[app][add_account] error adding data to db");
        console.log(error);
      })

    },
    );


    bus.$on('logout', () => {
      console.log("[app.vue][created][from bus][logout] changing app vue component : Home");
      localStorage.removeItem("user-token");
      this.data_to_component.logged_in_flag = false;
      this.data_to_component =  {"loading_flag":true,
                            "initial_load_flag":true,
                            "logged_in_flag":false,
                            "expense_data":'',
                            "user_data":{},
                            "account_data":{},
                            "category":{},
                            "categoryaccount":{},
                            "subcategory":{},
                            "subcategoryaccount":{},
                            "profile_image":{},
                            "profile":{},
                            "buffer_data":[],

                            },
      this.whichcomponent="Home";
    },
  );
  bus.$on('changedayfilter', (timefilter) => {
    console.log("[app][crated][from bus][changedayfilter] filter change ");
    console.log(timefilter);
    this.get_expense_data(timefilter);
  }
);
},
methods:{
  get_data_from_api: function(url,filter,to){
    console.log("[app][methods][get_data_from_api] ",filter);
    if (filter.lower_filter != null){
      url = url+filter.lower_filter;
    }
    if (filter.date_upper != null){
      url = url+filter.date_upper;
    }
    console.log("[app][method][get_data_from_api] connecting to api");
    const baseURI = url;
    console.log("[app][method][get_data_from_api] token :",this.token);
    console.log("[app][method][get_data_from_api] url :",url,filter);
    this.$http.get(baseURI,{
      headers: {
        Authorization: 'JWT '+this.token
      }
    })
    .then(response => {
      console.log("[app][method][get_data_from_api]",to," success!")
      console.log(response.data);
      console.log("[app][method][get_data_from_api] updating data to component ");
      this.data_to_component[to] = response.data
      if (to == "expense_data"){
        console.log("All api calls are successfull");
        this.data_to_component.loading_flag = false

      }
    })
    .catch(error => {
      console.log("[app][method][get_data_from_api]",to," failed!")
      console.log(error)
      this.data_to_component.logged_in_flag = false
      this.whichcomponent = "Home";
      localStorage.removeItem("user-token");
      this.data_to_component.logged_in_flag = false;

    })

  },
  get_user_data: function(){
    console.log("[app][method][get_user_data] getting user data");
    const baseURI = '/api/expense-tracker/user/';
    this.get_data_from_api(baseURI,{},"user_data");

  },
  get_category_data: function(){
    console.log("[app][method][get_category_data] getting category data");
    const baseURI = '/api/expense-tracker/category/';
    this.get_data_from_api(baseURI,{},"category");

  },
  get_subcategory_data: function(){
    console.log("[app][method][get_subcategory_data] getting subcategory data");
    const baseURI = '/api/expense-tracker/subcategory/';
    this.get_data_from_api(baseURI,{},"subcategory");

  },
  get_account_category_data: function(){
    console.log("[app][method][get_account_category_data] getting account category data");
    const baseURI = '/api/expense-tracker/categoryaccount/';
    this.get_data_from_api(baseURI,{},"categoryaccount");

  },
  get_account_subcategory_data: function(){
    console.log("[app][method][get_account_subcategory_data] getting account subcategory data");
    const baseURI = '/api/expense-tracker/subcategoryaccount/';
    this.get_data_from_api(baseURI,{},"subcategoryaccount");

  },
  get_account_data: function(){
    console.log("[app][method][get_account_data] getting account data");
    const baseURI = '/api/expense-tracker/account/';
    this.get_data_from_api(baseURI,{},"account_data");
  },
  get_profile_data: function(){
    console.log("[app][method][get_account_data] getting account data");
    const baseURI = '/api/expense-tracker/profile/';
    this.get_data_from_api(baseURI,{},"profile");
  },
  get_profile_image_data: function(){
    console.log("[app][method][get_profile_image_data] getting image data");
    const baseURI = '/api/expense-tracker/user-image/';
    this.get_data_from_api(baseURI,{},"profile_image");
  },
  get_expense_data: function(timeframe){
      console.log("[app][method][get_expense_data] getting expense data");
      console.log("[app][method][get_expense_data] filter:",timeframe);
      const baseURI = '/api/expense-tracker/expense/?';
      var d = new Date()
      var lower_filter = null;
      var upper_filter = null;
      var lower_day,lower_month,lower_year;
      var upper_day,upper_month,upper_year;
      console.log( upper_day,upper_month,upper_year,upper_filter);
      // var filter_lower,filter_upper;
      if (timeframe.time_filter_selected == "Day"){
        console.log("LLLLLLLLLLLL");
        lower_day = d.getDate();
        lower_month = d.getMonth();
        lower_year = d.getFullYear();
        lower_filter  = "create_on__gte="+lower_year+"-"+lower_month+"-"+lower_day + "T00%3A00%3A00Z&"
      }
      if (timeframe.time_filter_selected == "Month"){
        console.log("MMMMM");
        lower_day = 1;
        lower_month = d.getMonth();
        lower_year = d.getFullYear();
        upper_day = 1;

        upper_month = d.getMonth() +1
        upper_year = d.getFullYear();
        if (lower_month == 12){
          upper_month = 1;
          upper_year = upper_year +1 ;
        }
        lower_filter  = "create_on__gte="+lower_year+"-"+lower_month+"-"+lower_day + "T00%3A00%3A00Z&"
        upper_filter  = "create_on__lte="+upper_year+"-"+upper_month+"-"+upper_month + "T00%3A00%3A00Z&"

      }
      if (timeframe.time_filter_selected == "Month"){
        lower_day = 1;
        lower_month = d.getMonth();
        lower_year = d.getFullYear();
        upper_day = 1;
        upper_month = d.getMonth() +1
        upper_year = d.getFullYear();
        if (lower_month == 12){
          upper_month = 1;
          upper_year = upper_year +1 ;
        }
        lower_filter  = "create_on__gte="+lower_year+"-"+lower_month+"-"+lower_day + "T00%3A00%3A00Z&"
        upper_filter  = "create_on__lte="+upper_year+"-"+upper_month+"-"+upper_day + "T00%3A00%3A00Z&"

      }
      if (timeframe.time_filter_selected == "Year"){
        lower_day = 1;
        lower_month = 1;
        lower_year = d.getFullYear();
        upper_day = 1;
        upper_month =1
        upper_year = d.getFullYear() +1 ;

        lower_filter  = "create_on__gte="+lower_year+"-"+lower_month+"-"+lower_day + "T00%3A00%3A00Z&"
        upper_filter  = "create_on__lte="+upper_year+"-"+upper_month+"-"+upper_day + "T00%3A00%3A00Z&"

      }
      if (timeframe.time_filter_selected == "Week"){
        var end = d.getDate() - (d.getDay()-1)+6
        end =new Date(d.setDate(end)).toDateString()
        upper_day =  d.getDate(end);
        upper_month = d.getMonth(end);
        upper_year = d.getFullYear(end);
        var start  = d.getDate() -7
        start =new Date(d.setDate(start)).toDateString()
        lower_day = d.getDate(start);
        lower_month =  d.getMonth(start);
        lower_year = d.getFullYear(start);
        // last_day = last_day.toISOString().substr(0, 10),
        lower_filter  = "create_on__gte="+lower_year+"-"+lower_month+"-"+lower_day + "T00%3A00%3A00Z&"
        upper_filter  = "create_on__lte="+upper_year+"-"+upper_month+"-"+upper_day + "T00%3A00%3A00Z&"
      }
      this.get_data_from_api(baseURI,{lower_filter,upper_filter},"expense_data");
  },
},
}
</script>
