<template>
  <div class="account_class">
    <v-app-bar
      app
      dense
      dark

    >
      <v-app-bar-nav-icon v-on:click="call_sidebar()" >
        <v-icon>
          mdi-home
        </v-icon>
      </v-app-bar-nav-icon>
      <v-toolbar-title>
        Accounts
      </v-toolbar-title>
      <v-spacer ></v-spacer>
      <v-btn  icon>
        <v-icon >mdi-magnify</v-icon>
      </v-btn>
      <v-btn  v-on:click="add_dialog = true" icon>
        <v-icon>mdi-plus</v-icon>
      </v-btn>
  </v-app-bar>
  <v-main>
    <v-container>

          <v-row dense>
            <v-col
            v-for="account in this.data.account_data"
            v-bind:key="account.id"
            cols="12">
              <v-card

                :color="color[account.id % 3]"
                dark
              >
                <v-card-title class="headline">{{account.account_name}}</v-card-title>
                <v-card-subtitle>Balance : {{account.balance}}</v-card-subtitle>
                <v-card-actions>
                  <v-btn text>Edit</v-btn>
                  <v-dialog
                  :retain-focus="false"
                  v-model="delete_dialog" persistent >
                        <template v-slot:activator="{ on, attrs }">
                          <v-btn

                            text
                            dark
                            v-bind="attrs"
                            v-on="on"
                          >
                            Delete
                          </v-btn>
                        </template>
                        <v-card>
                          <v-card-title class="headline">Want to delete this ?</v-card-title>
                          <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="red darken-1" text v-on:click="delete_account(account)">Delete</v-btn>
                            <v-btn color="teal darken-1" text v-on:click="delete_dialog=false;">Cancel</v-btn>
                          </v-card-actions>
                        </v-card>
                      </v-dialog>
                </v-card-actions>
              </v-card>
            </v-col>
            <v-col>
              <v-dialog v-model="add_dialog" >
     <template v-slot:activator="{ on, attrs }">
       <v-btn
         color="primary"
         dark
         v-bind="attrs"
         v-on="on"
       >
         Add Account
       </v-btn>
     </template>
     <v-card>
       <v-card-title>
         <span class="headline">Account Details</span>
       </v-card-title>
       <v-card-text>
         <v-container>
           <v-row>
             <v-col cols="12" sm="6" md="4">
               <v-text-field
               v-bind:error-messages="error_message"
               v-model="new_account_name"
                label="Account name*" required></v-text-field>
             </v-col>
             <v-col cols="12" sm="6" md="4">
               <v-text-field
                v-model="new_account_balance"
                label="Balance" hint="Initial amount"></v-text-field>
             </v-col>
           </v-row>
         </v-container>
         <small>*indicates required field</small>
       </v-card-text>
       <v-card-actions>
         <v-spacer></v-spacer>
         <v-btn color="red darken-1" text @click="add_dialog = false">Close</v-btn>
         <v-btn color="blue darken-1" text @click="add_account">Save</v-btn>
       </v-card-actions>
     </v-card>
   </v-dialog>
 </v-col>
          </v-row>
        </v-container>

      </v-main>

  </div>
</template>
<script>
import { bus } from '../../main.js';

export default {
  data: function() {
    return {
      token: localStorage.getItem('user-token') || '',
      color : ['#1F7087','#952175',"#385F73"],
      delete_dialog:false,
      add_dialog:false,
      new_account_name:"",
      new_account_balance : 0,
      error_message : "",
    }
  },
  props: [
    "data"
  ],
  watch: {
     '$props':{
       handler: function (val, oldVal) {
         console.log('[account][watch] new', val)
         console.log("[account][watch] old",oldVal);
       },
       deep: true
     }
   },
  methods:{
    call_sidebar: function(){
      console.log("[account][methods][call_sidebar] Truning on sidebar");
      bus.$emit('sidebar');
    },
    add_account: function(){

      if(this.new_account_name == ""){
        this.error_message = "Please fill in account name"
        console.log("[account][methods][add_account] cant add account");
        return
      }
      this.add_dialog = false;
      console.log("[account][methods][add_account] Adding new account ");
      this.error_message = ""
      bus.$emit('add_account',{"account_name":this.new_account_name,"balance":this.new_account_balance,"profile":this.data.profile[0].id});
      this.new_account_name = ""
    },
    delete_account: function(account){
      this.delete_dialog = false
      console.log("[account][methods][delete_account] Deleting account ",account);
      bus.$emit('delete_one_account',account);
    },
  }
}
</script>
