  <template>
    <div >

      <div v-if="typeof this.data.expense_data == 'object'" >
        <div class="" v-if="filteredexpense[1]==0">
          <v-banner  small v-model="v0" single-line transition="slide-y-transition">
             No entry found, clear search field 

           </v-banner>
        </div>

        <div v-if="Object.keys(this.data.expense_data).length != 0 ">
          <v-expansion-panels
          insert
          >
            <v-expansion-panel
            v-for="item in orderBy(filteredexpense[0],orderdata,des)" v-bind:key="item.id"
            v-on:click="clicked_card(item,item.note,item.id)"
            ><v-expansion-panel-header>
              <v-list-item outlined rounded>
                <v-list-item-avatar color="grey lighten-4">
                  <v-icon>
                    mdi-{{  get_category_icon(item.category)}}
                  </v-icon>
                </v-list-item-avatar>
                <v-list-item-content>
                  <v-list-item-title >
                    {{get_category(item.category)}}- {{get_subcategory(item.sub_category)}}
                    <span v-if="item.type == 'expense'" class="float-right red--text">
                      {{item.amount}}
                    </span>
                    <span v-else class="green--text float-right">
                      {{item.amount}}
                    </span>
                  </v-list-item-title>
                  <v-list-item-subtitle class="text-sm-body-2">
                    {{item.create_on.slice(0,10)}}   {{  item.note | to-slice}}
                    <span class="float-right text-warning">
                      {{get_account(item.account)}}
                    </span>
                  </v-list-item-subtitle>
                </v-list-item-content>
              </v-list-item>
            </v-expansion-panel-header>
            <v-expansion-panel-content>
              <v-row no-gutters>
                <v-col background-color="red"
                cols="6"
                sm="6"
                >
                {{  item.note }}
                </v-col>
                <v-col background-color="red"
                cols="3"
                sm="6"
                >
          <v-dialog v-model="delete_dialog" persistent >
                <template v-slot:activator="{ on, attrs }">
                  <v-btn

                    text
                    small
                    outlined
                    color="primary"
                    dark
                    v-bind="attrs"
                    v-on="on"
                  >
                    Delete
                  </v-btn>
                </template>
                <v-card>
                  <v-card-title class="headline">Want to delete this ?</v-card-title>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red darken-1" text v-on:click="delete_expense(item)">Delete</v-btn>
                    <v-btn color="teal darken-1" text v-on:click="delete_dialog=false">Cancel</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>


              </v-col>
              <v-col background-color="red"
              cols="3"
              sm="6"

              >
              <v-btn
              small
            text
            color="primary"
          >
            Edit
          </v-btn>
            </v-col>
          </v-row>
          </v-expansion-panel-content>

          </v-expansion-panel>
        </v-expansion-panels>
      </div>
    </div>

    <div v-else>
      loading data
    </div>
  </div>
  </template>

  <script>

  import { bus } from '../../main.js';
  import Vue2Filters from 'vue2-filters'
  export default {
    name: 'home_expense_list',
    mixins: [Vue2Filters.mixin],
    data: function() {
      return {
        orderdata:"create_on",
        delete_dialog:false,

        des : -1,
        timeformat:"day",
        v0:true,

      }
    },
    props: [
      "data","search"
    ],
    watch: {
      '$props':{
        handler: function (val, oldVal) {
          console.log('[expense][watch] new', val)
          var expense;
          for (expense in this.data.expense_data) {
            this.data.expense_data[expense]["show"]=false;
          }
          this.search = this.search.toLowerCase();
          console.log("[expense][watch] old",oldVal);
        },
        deep: true
      }
    },
    created() {
      console.log(Object.keys(this.data.expense_data).length);

      bus.$on('sort_expense', (order) => {
        console.log("[expense.vue][created][from bus][orderby] changing expense order ",order);
        this.orderdata = order.value;
        this.des = order.order;
        });
      // this.filteredexpense()

    },
    methods:{
      clicked_card: function(item,note,id){
        console.log(item,note,id);

      },
      change_tab: function(){
        bus.$emit('changecomponent','report');
      },
      delete_expense: function(item){
        this.delete_dialog = false;
        bus.$emit('delete_expense',item.id);
        bus.$emit('modify_account',item);
        bus.$emit('changedayfilter',{'time_filter_selected':"Day",'offset':null,'start_day':null,'end_day':null});


      },
      get_category: function(category_id){
        // return "helo"+category_id
        var cate;
        for (cate in this.data.category){
          if(this.data.category[cate].id == category_id){
            return this.data.category[cate].category_name
          }
        }
        return "----"
      },
      get_category_icon: function(category_id){
        // return "helo"+category_id
        var cate;
        for (cate in this.data.category){
          if(this.data.category[cate].id == category_id){
            return this.data.category[cate].icon
          }
        }
        return "----"
      },
      get_subcategory: function(subcategory_id){
        // return "helo"+category_id
        var cate;
        for (cate in this.data.subcategory){
          if(this.data.subcategory[cate].id == subcategory_id){
            return this.data.subcategory[cate].subcategory_name
          }
        }
        return "----"
      },

      get_account: function(account_id){
        // return "helo"+category_id
        var cate;
        for (cate in this.data.account_data){
          if(this.data.account_data[cate].id == account_id){
            return this.data.account_data[cate].account_name
          }
        }
        return "----"

      },
    },
    computed: {
      filteredexpense: function(){
        console.log("[expense][computed][filteredexpense] changed data for expense  ..");
        var test = this.data.expense_data.filter((each_expense_data) => {
          return each_expense_data.note.toLowerCase().match(this.search);
        });
        var objectLength = Object.keys(test).length;
        console.log("[expense][computed][filteredexpense] len of com ",objectLength);
        return [test,objectLength];
      },
    }
  }
  </script>
  <style lang="scss" scoped>
  .card{
    text-align: left;
  }
  .v-expansion-panel-header{
    padding:0;
  }
  .md-toolbar + .md-toolbar {
    margin-top: 16px;
  }
  </style>
