<template>
  <div class="about">
  <v-app-bar
    app
    dense
    dark

  >
    <v-app-bar-nav-icon v-on:click="call_sidebar()" >

      <v-icon>
        mdi-home
      </v-icon>
    </v-app-bar-nav-icon>
    <v-progress-linear
    :active="loading"
    :indeterminate="loading"
    absolute
    bottom
    color="deep-purple accent-4"
    ></v-progress-linear>
    <v-toolbar-title>
      About
    </v-toolbar-title>
    <v-spacer ></v-spacer>
    <v-btn  v-on:click="get_data_from_api()" icon>
      <v-icon>mdi-refresh</v-icon>
    </v-btn>

</v-app-bar>
<v-main>
  <v-timeline>
    <v-timeline-item
      v-for="(item, i) in app_history"
      :key="i"
      :color="color_list[i%5]"
      small
    >
      <template v-slot:opposite>
        <span
          :class="`headline font-weight-bold ${color_list[i%5]}--text`"
          v-text="item.create_on"
        ></span>
      </template>
      <v-card
    class="mx-auto"
    max-width="344"
  >
    <v-img
      v-bind:src="item.image"
      height="200px"
    ></v-img>

    <v-card-title>
      {{item.tittle}}
    </v-card-title>

    <v-card-subtitle>
      {{item.notes}}

    </v-card-subtitle>
  </v-card>
    </v-timeline-item>
  </v-timeline>
</v-main>

</div>
</template>

<script>
import { bus } from '../../main.js';

  export default {
    data: () => ({
      token: localStorage.getItem('user-token') || '',
      color_list: ["cyan","green","pink","amber","orange"],
      app_history :{},
      loading:true,

    }),
    created: function(){
      this.get_data_from_api();
    },
    methods:{
      get_data_from_api: function(){
        console.log("[about][methods][get_data_from_api] connecting to api");
        const baseURI = "/api/apps/apphistory/?app_name=1";
        console.log("[about][method][get_data_from_api] token :",this.token);
        console.log("[about][method][get_data_from_api] url :",baseURI);
        this.$http.get(baseURI,{
          headers: {
            Authorization: 'JWT '+this.token
          }
        })
        .then(response => {
          console.log("[about][method][get_data_from_api] success!")
          console.log(response.data);
          this.app_history = response.data
          this.loading  = false

        })
        .catch(error => {
          console.log("[about][method][get_data_from_api]  failed!")
          console.log(error)
          this.whichcomponent = "Home";

        })

      },

      call_sidebar: function(){
        console.log("[about][methods][call_sidebar] Truning on sidebar");
        bus.$emit('sidebar');
      },
  }
}
</script>
