<template>
  <div>
      <v-navigation-drawer app bottom
        v-model="drawer" class='teal'
      >
        <v-list
          dense nav class="py-0"
        >
          <v-list-item two-line :class="miniVariant=false && 'px-0'">
            <v-list-item-avatar v-on:click="to_profile">
              <img v-bind:src="data.profile_image[0].image">
            </v-list-item-avatar>

            <v-list-item-content>
              <v-list-item-title v-on:click="to_profile">{{data.user_data[0].username}}</v-list-item-title>
              <v-list-item-subtitle>{{data.user_data[0].email}}</v-list-item-subtitle>
            </v-list-item-content>
          </v-list-item>

          <v-divider></v-divider>

          <v-list-item
            v-for="item in items"
            :key="item.title"
            link
            v-on:click="switch_main(item.title)"
          >
            <v-list-item-icon>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-item-icon>

            <v-list-item-content>
              <v-list-item-title>{{ item.title }}</v-list-item-title>
            </v-list-item-content>
          </v-list-item>
        </v-list>
        <template v-slot:append>
          <div class="pa-2">
            <v-btn v-on:click="logout()" block>Logout</v-btn>
          </div>
        </template>
      </v-navigation-drawer>
  </div>
</template>

<script>
import { bus } from '../../main.js';

  export default {
    name: 'sidebar',
    data () {
      return {
        drawer: true,
        items: [
          { title: 'Home', icon: 'mdi-home' },
          { title: 'Search', icon: 'mdi-magnify' },
          { title: 'Accounts', icon: 'mdi-bank' },
          { title: 'Budget', icon: 'mdi-battery-40' },
          { title: 'Report', icon: 'mdi-chart-areaspline' },
          { title: 'About', icon: 'mdi-information-outline' },
          { title: 'Fedback', icon: 'mdi-comment-quote-outline' },

        ],
      }
    },
    props: [
      "data","search"
    ],
    watch: {
      '$props':{
        handler: function (val, oldVal) {
          console.log('[expense][watch] new', val)
          console.log("[expense][watch] old",oldVal);
        },
        deep: true
      }
    },


    created:function(){
      bus.$on('sidebar', () => {
        console.log("[sidebar.vue][created][from bus][sidebar] open sidebar");
        this.drawer = true;
        }
      );
    },
    computed: {

    },
    methods:{

      switch_main: function(data){
          console.log("[sidebar][methods][change tab] moving to",data);

          this.drawer = false;

          bus.$emit('changecomponent',data);
      },
      logout: function(){
        console.log("[sidebar][methods][change tab] logging out");
        bus.$emit('logout');

      },

      to_profile: function(){
        console.log("[login][methods][to_profile] redirecion to django profile");
        window.location.href = '/profile/';


      },
    }
  }
</script>



