<template>

  <v-container>
    <v-layout wrap>
      <v-flex sm12 md6 offset-md3>
        <v-card elevation="4" light tag="section">
          <v-card-title>
            <v-layout align-center justify-space-between>
              <h3 class="headline">
                {{ platformName }}
              </h3>
                <v-icon large>mdi-alien-outline</v-icon>
            </v-layout>
          </v-card-title>
          <v-divider></v-divider>
          <v-card-text>
            <p>Sign in with your username and password:</p>
            <v-form  class="login" @submit.prevent="do_login">
              <v-text-field
                            outline
                            label="Username"
                            type="text"
                            v-bind:error-messages="login_error"
                            :rules="[rules.required]"
                            v-model="username"></v-text-field>
              <v-text-field
                            outline
                            hide-details
                            label="Password"
                            type="password"
                            :rules="[rules.required]"
                            v-model="password"></v-text-field>
            </v-form>
          </v-card-text>
          <v-divider></v-divider>
          <v-card-actions :class="{ 'pa-3': $vuetify.breakpoint.smAndUp }">
            <v-btn  v-on:click="to_regester" color="teal" flat>
              Regester
            </v-btn>
            <v-spacer></v-spacer>
            <v-btn  v-on:click="do_login" color="info" :large="$vuetify.breakpoint.smAndUp">
              <v-icon left>lock</v-icon>
              Login
            </v-btn>
          </v-card-actions>
        </v-card>
      </v-flex>
      <v-flex sm12 md6 offset-md3>
        <v-layout align-center justify-space-between>
          <p class="caption my-3">
            <a href="http://sunilbp96.pythonanywhere.com/password-reset/">Forgot password</a>
            |
            <a href="#">Home page </a>
          </p>
          <p class="caption my-3">Powered by <a href="#">Boredom</a></p>
        </v-layout>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>


import { bus } from '../../main.js';

export default {
  name: 'login',
  data: function() {
  return {
    token: localStorage.getItem('user-token') || '',
    darkTheme: true,
     platformName: 'Expense Vue',
     password: null,
     username: null,
     login_error: null,
     rules: {
       required: value => !!value || 'Required.',
       counter: value => value.length <= 20 || 'Max 20 characters',
            },
          }
        },
  props: {

  },
  methods:{
    to_regester: function(){
      console.log("[login][methods][to_regester] redirecion to django regester");
      window.location.href = 'http://sunilbp96.pythonanywhere.com/register/';


    },
    do_login: function(){
      const baseURI = '/api/expense-tracker/token-auth/';
      console.log(this.username);
      if (this.username ==null && this.password == null  ){
          console.log("no user name ");
          return
      }
      this.$http.post(baseURI, {
        username: this.username,
        password: this.password
        })
      .then(response => {
        console.log("[login][do_login] success switching to home expense");
        console.log(response.data.token);
        localStorage.setItem("user-token",response.data.token);
        bus.$emit('login');

      })
      .catch(error => {
        console.log(error)
        console.log(error.body.non_field_errors);
        this.password = ""
        localStorage.removeItem("user-token");
        this.login_error = "Username or password in incorrect "
      })
    },
    },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.v-btn,.v-card{
  "border-radius":4px
}
.v-card__title{
  "text-transform":uppercase
}
</style>
