<template>
  <div class="expense_class">
    <v-toolbar dark flat  >
      <v-app-bar-nav-icon v-on:click="call_sidebar()" >
        <v-icon>
          mdi-home
        </v-icon>
      </v-app-bar-nav-icon>
      <v-toolbar-title>
        Add Expense
      </v-toolbar-title>
      <template v-slot:extension>
        <v-tabs
        v-model="tabs_selected"
        class="elevation-2"
        dark
        dense
        align-with-title
        center-active
        grow
        >
        <v-tabs-slider color="teal"></v-tabs-slider>
        <v-tab class="text-body-2" v-for="i in tabs" :key="i" >
          {{ i }}
        </v-tab>

      </v-tabs>
    </template>
  </v-toolbar>
  <v-tabs-items v-model="tabs_selected">
    <v-tab-item >
      <v-card  tile color="teal lighten-3" >
        <v-text-field v-model="expense_amount"
        class="pd-10"
        label="Amount"
        v-bind:error-messages="expense_error"
        :rules="[rules.numbers]"
        prepend-inner-icon="mdi-cash-usd-outline"
        outlined
        background-color=""
        >
      </v-text-field>
      <v-divider></v-divider>
      <v-container >
        <v-row no-gutters>
          <v-col
          cols="6"
          sm="12"
          >
          <v-menu
          v-model="expense_menu_date"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="290px"
          >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
            v-model="expense_date"
            label="Select date"
            prepend-icon="mdi-calendar-range"
            readonly
            v-bind="attrs"
            v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker v-model="expense_date" @input="expense_menu_date = false"></v-date-picker>
        </v-menu>
      </v-col>
      <v-col
      cols="6"
      sm="12"
      >
      <v-menu
      ref="menu"
      v-model="expense_menu_time"
      :close-on-content-click="false"
      :nudge-right="40"
      :return-value.sync="expense_time"
      transition="scale-transition"
      offset-y
      max-width="290px"
      min-width="290px"
      >
      <template v-slot:activator="{ on, attrs }">
        <v-text-field
        v-model="expense_time"
        label="Select time "
        prepend-icon="mdi-clock-outline"

        readonly
        v-bind="attrs"
        v-on="on"
        ></v-text-field>
      </template>
      <v-time-picker
      v-if="expense_menu_time"
      v-model="expense_time"
      full-width

      ampm-in-title
      @click:minute="$refs.menu.save(expense_time)"
      ></v-time-picker>
    </v-menu>
  </v-col>
</v-row>
</v-container>
<v-card flaten outlined  class="rounded-t-xl" >
  <v-card-title>
    <v-select
    hide-details="auto"
    :items="this.data.account_data"
    v-model="expense_account"
    label="Account"
    outlined
    prepend-inner-icon="account_balance"
    item-text="account_name"
    >
  </v-select>
  <v-row no-gutters>
    <v-col
    cols="12"
    class="pa-5"
    >
    <v-dialog v-model="expense_dialog" fullscreen hide-overlay
    content-class="v-dialog--custom"
    transition="dialog-bottom-transition">
    <template v-slot:activator="{ on, attrs }">
      <v-btn
      color="primary"
      dark
      v-bind="attrs"
      v-on="on"
      >
      Category {{expense_category.category_name}}:{{expense_subcategory.subcategory_name}}
    </v-btn>

  </template>
  <v-card>
    <v-toolbar dark >
      <v-btn icon dark @click="expense_dialog = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
      <v-toolbar-title>Category</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items>
        <v-btn dark text @click="expense_dialog = false">Save</v-btn>
      </v-toolbar-items>
    </v-toolbar>

    <v-row no-gutters>
      <v-col background-color="red"
      cols="6"
      sm="6"
      >
      <v-list
      dense
      shaped
      avatar
      >
      <v-list-item-group color="primary">
        <v-list-item
        v-for="(item) in filteredexpensecategory"
        :key="item.id"
        v-on:click="log_cate(item)"

        >
        <v-list-item-content>
          <v-list-item-title>{{item.category_name}}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list-item-group>
  </v-list>
</v-col>
<v-col background-color="red"
cols="6"
sm="6"
>
<v-list
dense
shaped
avatar
>
<v-list-item-group color="primary">
  <v-list-item
  v-for="(item) in filteredexpensesubcategory"
  :key="item.id"

  v-on:click="log_subcate(item)"
  >
  <v-list-item-content>
    <v-list-item-title>{{item.subcategory_name}}</v-list-item-title>
  </v-list-item-content>
</v-list-item>
</v-list-item-group>
</v-list>

</v-col>
</v-row>
</v-card>
</v-dialog>
</v-col>
</v-row>


<v-text-field
label="Note"
outlined
v-model="expense_note"
prepend-inner-icon="note"
dense
></v-text-field>
</v-card-title>
</v-card>
</v-card>
</v-tab-item>
<v-tab-item >
  <v-card  tile color="green lighten-3" >
    <v-text-field v-model="income_amount"
    class="pd-10"
    v-bind:error-messages="income_error"

    label="Amount"
    :rules="[rules.numbers]"
    prepend-inner-icon="mdi-cash-usd-outline"
    outlined

    background-color=""
    >
  </v-text-field>
  <v-divider></v-divider>
  <v-container >
    <v-row no-gutters>
      <v-col
      cols="6"
      sm="12"
      >
      <v-menu
      v-model="income_menu_date"
      :close-on-content-click="false"
      :nudge-right="40"
      transition="scale-transition"
      offset-y
      min-width="290px"
      >
      <template v-slot:activator="{ on, attrs }">
        <v-text-field
        v-model="income_date"
        label="Select date"
        prepend-icon="mdi-calendar-range"
        readonly
        v-bind="attrs"
        v-on="on"
        ></v-text-field>
      </template>
      <v-date-picker v-model="income_date" @input="income_menu_date = false"></v-date-picker>
    </v-menu>
  </v-col>
  <v-col
  cols="6"
  sm="12"
  >
  <v-menu
  ref="menu"
  v-model="income_menu_time"
  :close-on-content-click="false"
  :nudge-right="40"
  :return-value.sync="income_time"
  transition="scale-transition"
  offset-y
  max-width="290px"
  min-width="290px"
  >
  <template v-slot:activator="{ on, attrs }">
    <v-text-field
    v-model="income_time"
    label="Select time "
    prepend-icon="mdi-clock-outline"

    readonly
    v-bind="attrs"
    v-on="on"
    ></v-text-field>
  </template>
  <v-time-picker
  v-if="income_menu_time"
  v-model="income_time"
  full-width

  ampm-in-title
  @click:minute="$refs.menu.save(income_time)"
  ></v-time-picker>
</v-menu>
</v-col>
</v-row>
</v-container>
<v-card flaten outlined  class="rounded-t-xl" >
  <v-card-title>
    <v-select
    hide-details="auto"
    :items="this.data.account_data"
    v-model="income_account"
    label="Account"
    outlined
    prepend-inner-icon="account_balance"
    item-text="account_name"
    >
  </v-select>
  <v-row no-gutters>
    <v-col
    cols="12"
    class="pa-5"
    >
    <v-dialog v-model="income_dialog" fullscreen hide-overlay
    content-class="v-dialog--custom"
    transition="dialog-bottom-transition">
    <template v-slot:activator="{ on, attrs }">
      <v-btn
      color="primary"
      dark
      v-bind="attrs"
      v-on="on"
      >
      Category {{income_category.category_name}}:{{income_subcategory.subcategory_name}}
    </v-btn>

  </template>
  <v-card>
    <v-toolbar dark >
      <v-btn icon dark @click="income_dialog = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
      <v-toolbar-title>Category</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items>
        <v-btn dark text @click="income_dialog = false">Save</v-btn>
      </v-toolbar-items>
    </v-toolbar>

    <v-row no-gutters>
      <v-col background-color="red"
      cols="6"
      sm="6"
      >
      <v-list
      dense
      shaped
      avatar
      >
      <v-list-item-group color="primary">
        <v-list-item
        v-for="(item) in filteredincomecategory"
        :key="item.id"
        v-on:click="log_income_cate(item)"

        >
        <v-list-item-content>
          <v-list-item-title>{{item.category_name}}</v-list-item-title>
        </v-list-item-content>
      </v-list-item>
    </v-list-item-group>
  </v-list>
</v-col>
<v-col background-color="red"
cols="6"
sm="6"
>
<v-list
dense
shaped
avatar
>
<v-list-item-group color="primary">
  <v-list-item
  v-for="(item) in filteredincomesubcategory"
  :key="item.id"

  v-on:click="log_income_subcate(item)"
  >
  <v-list-item-content>
    <v-list-item-title>{{item.subcategory_name}}</v-list-item-title>
  </v-list-item-content>
</v-list-item>
</v-list-item-group>
</v-list>

</v-col>
</v-row>
</v-card>
</v-dialog>
</v-col>
</v-row>


<v-text-field
label="Note"
outlined
v-model="income_note"
prepend-inner-icon="note"
dense
></v-text-field>
</v-card-title>
</v-card>
</v-card>
</v-tab-item>
<v-tab-item >
  <v-card  tile color="yellow lighten-3" >
    <v-text-field v-model="transfer_amount"
    class="pd-10"
    label="Amount"
    v-bind:error-messages="transfer_error"

    :rules="[rules.numbers]"
    prepend-inner-icon="mdi-cash-usd-outline"
    outlined

    background-color=""
    >
  </v-text-field>
  <v-divider></v-divider>
  <v-container >
    <v-row no-gutters>
      <v-col
      cols="6"
      sm="12"
      >
      <v-menu
      v-model="transfer_menu_date"
      :close-on-content-click="false"
      :nudge-right="40"
      transition="scale-transition"
      offset-y
      min-width="290px"
      >
      <template v-slot:activator="{ on, attrs }">
        <v-text-field
        v-model="transefer_date"
        label="Select date"
        prepend-icon="mdi-calendar-range"
        readonly
        v-bind="attrs"
        v-on="on"
        ></v-text-field>
      </template>
      <v-date-picker v-model="transefer_date" @input="transfer_menu_date = false"></v-date-picker>
    </v-menu>
  </v-col>
  <v-col
  cols="6"
  sm="12"
  >
  <v-menu
  ref="menu"
  v-model="transfer_menu_time"
  :close-on-content-click="false"
  :nudge-right="40"
  :return-value.sync="transfer_time"
  transition="scale-transition"
  offset-y
  max-width="290px"
  min-width="290px"
  >
  <template v-slot:activator="{ on, attrs }">
    <v-text-field
    v-model="transfer_time"
    label="Select time "
    prepend-icon="mdi-clock-outline"

    readonly
    v-bind="attrs"
    v-on="on"
    ></v-text-field>
  </template>
  <v-time-picker
  v-if="transfer_menu_time"
  v-model="transfer_time"
  full-width

  ampm-in-title
  @click:minute="$refs.menu.save(transfer_time)"
  ></v-time-picker>
</v-menu>
</v-col>
</v-row>
</v-container>
<v-container >
  <v-row no-gutters>
    <v-col
    cols="6"
    sm="12"
    ><v-select
    hide-details="auto"
    :items="this.data.account_data"
    v-model="transfer_account_from"
    label="From "
    outlined
    prepend-inner-icon="account_balance"
    item-text="account_name"
    >
  </v-select>
  </v-col>
  <v-col
  cols="6"
  sm="12"
  ><v-select
  hide-details="auto"
  :items="this.data.account_data"
  v-model="transfer_account_to"
  :rules="[rules.same]"
  label="To "
  outlined
  prepend-inner-icon="account_balance"
  item-text="account_name"
  >
</v-select>
</v-col>
</v-row>
</v-container>

<v-card flaten outlined  class="rounded-t-xl" >
  <v-card-title>

    <v-text-field
    label="Note"
    outlined
    dense
    v-model="transfer_note"
    ></v-text-field>
  </v-card-title>
</v-card>
</v-card>
</v-tab-item>
</v-tabs-items>
<v-bottom-navigation
absolute
color="teal"
horizontal
>
<v-btn v-on:click="to_home">
  <span>Cancel</span>
  <v-icon>mdi-close</v-icon>
</v-btn>

<v-btn v-on:click="save_expense">
  <span>Save</span>
  <v-icon>mdi-content-save</v-icon>
</v-btn>

<v-btn>
  <span>Add another</span>
  <v-icon>mdi-plus</v-icon>
</v-btn>
</v-bottom-navigation>
</div>
</template>
<script>
import { bus } from '../../main.js';

export default {
  name: 'expense',
  data: function() {
    return {
      token: localStorage.getItem('user-token') || '',
      tabs:["Expense","Income","Transfer"],
      tabs_selected:'Income',
      income_amount:'',
      expense_amount:'',
      transfer_amount:'',
      income_date: new Date().toISOString().substr(0, 10),
      expense_date: new Date().toISOString().substr(0, 10),
      transefer_date: new Date().toISOString().substr(0, 10),
      income_menu_date: false,
      income_menu_time: false,
      expense_menu_date: false,
      expense_menu_time: false,
      transfer_menu_date: false,
      transfer_menu_time: false,
      income_time: null,
      expense_time: null,
      transfer_time: null,
      expense_account:null,
      income_account:null,
      transfer_account_from:null,
      transfer_account_to:null,

      expense_note:null,
      income_note:null,
      transfer_note:null,

      expense_category:{
        "category_name":"----",
        "id":"1"
      },
      expense_subcategory:{
        "subcategory_name":"----",
      },

      income_category:{
        "category_name":"----",
        "id":"1"
      },
      income_subcategory:{
        "subcategory_name":"----",
      },


      expense_dialog:false,
      income_dialog:false,
      income_error:"",
      expense_error:"",
      transfer_error:"",



      rules: {
        required: value => !!value || 'Required.',
        counter: value => value.length <= 20 || 'Max 20 characters',
        numbers: value=> {
          const pattern =  /^\d+$/;
          return pattern.test(value) || 'Only digits !'
        },
        same: value=>{
          console.log(">>>????");
          console.log(value);
          if (value == this.transfer_account_from){
            return "Same as from !";
          }
          else{
            return true;
          }
        }

      },

    }
  },
  props: [
    "data"
  ],
  created(){
    console.log("in created ");
    console.log(this.data);
    var time = new Date().toISOString().substr(11, 12);
    var split_time = time.toString().split(":")
    time = split_time[0]+":"+split_time[1]
    this.income_time = time
    this.expense_time = time
    this.transfer_time = time

  },
  methods:{
    initial_data_set: function(){
    this.tabs_selected='Income';
    this.income_amount='';
    this.expense_amount='';
    this.transfer_amount='';
    this.income_date= new Date().toISOString().substr(0, 10);
    this.expense_date= new Date().toISOString().substr(0, 10);
    this.transefer_date= new Date().toISOString().substr(0, 10);
    this.income_menu_date= false;
    this.income_menu_time= false;
    this.expense_menu_date= false;
    this.expense_menu_time= false;
    this.transfer_menu_date= false;
    this.transfer_menu_time= false;
    this.expense_account=null;
    this.income_account=null;
    this.transfer_account_from=null;
    this.transfer_account_to=null;

    this.expense_note=null;
    this.income_note=null;
    this.transfer_note=null;
    var time = new Date().toISOString().substr(11, 12);
    var split_time = time.toString().split(":")
    time = split_time[0]+":"+split_time[1];
    this.income_time = time;
    this.expense_time = time;
    this.transfer_time = time;
    this.expense_category = {
      "category_name":"----",
      "id":"1"
    };
    this.expense_subcategory = {
      "subcategory_name":"----",
    };
    this.income_error = "";
    this.expense_error = "";
    this.transfer_error = "";

    this.income_category  = {
      "category_name":"----",
      "id":"1"
    };
    this.income_subcategory= {
      "subcategory_name":"----",
    };

    },

    to_home : function(){
      console.log("[home][methods][to_home] Changing to home");
      this.initial_data_set()
      bus.$emit('changecomponent',"Home");
    },

    save_expense : function(){
      console.log("[home][methods][save_expense] saving data to db");

      if (this.tabs_selected == 0){
        if(this.expense_amount=="" ||
        this.expense_account==null ||
        this.expense_category == {
          "category_name":"----",
          "id":"1"
        } ||
        this.expense_subcategory == {
          "subcategory_name":"----",
        }
      ){
        this.expense_error = "PLeas fill in all details "
        return
      }
        console.log("[home][methods][save_expense] Expense ");

        var  temp_expense_account ;
        var  i;
        for (i in this.data.account_data){
          if(this.data.account_data[i].account_name == this.expense_account){
            temp_expense_account = this.data.account_data[i].id
          }
        }
        bus.$emit('add_expense',
        {
        "create_on": this.expense_date + "T"+ this.expense_time+"Z",
        "type": "expense",
        "amount": this.expense_amount,
        "note": this.expense_note,
        "profile": this.data.profile[0].id,
        "account": temp_expense_account,
        "category": this.expense_category.id,
        "sub_category": this.expense_subcategory.id
        }

      )
      }
      else if (this.tabs_selected == 1){
        if(this.income_amount=="" ||
        this.income_account==null ||
        this.income_category == {
          "category_name":"----",
          "id":"1"
        } ||
        this.income_subcategory == {
          "subcategory_name":"----",
        }
      ){
        this.income_error = "PLease fill in all details "
        return
      }
        console.log("[home][methods][save_expense] Income ");
        var  temp_income_account ;
        var  i_income;
        for (i_income in this.data.account_data){
          if(this.data.account_data[i_income].account_name == this.income_account){
            temp_income_account = this.data.account_data[i_income].id
          }
        }
        bus.$emit('add_expense',
        {
        "create_on": this.income_date + "T"+ this.income_time+"Z",
        "type": "income",
        "amount": this.income_amount,
        "note": this.income_note,
        "profile": this.data.profile[0].id,
        "account": temp_income_account,
        "category": this.income_category.id,
        "sub_category": this.income_subcategory.id
        }

      )
      }
      else {
        if(this.transfer_amount=="" ||
        this.transfer_account_from==null ||
        this.transfer_account_to==null
      ){
        this.transfer_error = "Please fill in all details "
        return
      }
        console.log("[home][methods][save_expense] Transfer ");
        var i_t;
        var  temp_transfer_account_from ;
        var  temp_transfer_account_to ;
        for (i_t in this.data.account_data){
          if(this.data.account_data[i_t].account_name == this.transfer_account_from){
            temp_transfer_account_from = this.data.account_data[i_t].id
          }
        }
        for (i_t in this.data.account_data){
          if(this.data.account_data[i_t].account_name == this.transfer_account_to){
            temp_transfer_account_to = this.data.account_data[i_t].id
          }
        }
        bus.$emit('transfer_amount',
        {
        "create_on": this.transefer_date + "T"+ this.transfer_time+"Z",
        "amount": this.transfer_amount,
        "note": this.transfer_note,
        "profile": this.data.profile[0].id,
        "from_account": temp_transfer_account_from,
        "to_account": temp_transfer_account_to,
        }

      )
      }
      this.initial_data_set()
      bus.$emit('changecomponent',"Home");
      bus.$emit('changedayfilter',{'time_filter_selected':"Day",'offset':null,'start_day':null,'end_day':null});

    },

    call_sidebar: function(){
      console.log("[home][methods][call_sidebar] Truning on sidebar");
      bus.$emit('sidebar');
      console.log(this.time);
      console.log(this.time2);
    },
    log_cate: function(data){
      console.log(">>");
      console.log(data);
      this.expense_category = data
      console.log(this.expense_category);
    },
    log_subcate: function(data){
      console.log(">>");
      console.log(data);
      this.expense_subcategory = data
      this.expense_dialog = false
      console.log(this.expense_category);
    },
    log_income_cate: function(data){
      console.log(">>");
      console.log(data);
      this.income_category = data
      console.log(this.income_category);
    },
    log_income_subcate: function(data){
      console.log(">>");
      console.log(data);
      this.income_subcategory = data
      this.income_dialog = false
      console.log(this.income_category);
    },


  },
  computed: {
    filteredexpensecategory: function(){
      return this.data.category.filter((each_cate_data) => {
        return each_cate_data.type.toLowerCase().match("expense");
      });
    },
    filteredexpensesubcategory: function(){
      return this.data.subcategory.filter((each_cate_data) => {
        if (this.expense_category){
        if (each_cate_data.parent == this.expense_category.id){
          return true
        }
        else{
          return false
        }
      }
      else{
        return false
      }
      });
    },
    filteredincomecategory: function(){
      return this.data.category.filter((each_cate_data) => {
        return each_cate_data.type.toLowerCase().match("income");
      });
    },
    filteredincomesubcategory: function(){
      return this.data.subcategory.filter((each_cate_data) => {
        if (this.income_category){
        if (each_cate_data.parent == this.income_category.id){
          return true
        }
        else{
          return false
        }
      }
      else{
        return false
      }
      });
    },
  }
}
</script>

