<template>
  <v-card  >
    <v-app-bar
      app
      dense
      dark

    >
      <v-app-bar-nav-icon v-on:click="call_sidebar()" ></v-app-bar-nav-icon>
      <v-progress-linear
      :active="loading"
      :indeterminate="loading"
      absolute
      bottom
      color="deep-purple accent-4"
      ></v-progress-linear>

      <v-select v-if="!search_buttom"  dense  hide-details
      v-on:change="change_time_filter()"
      v-model="time_filter_selected"
      :items="time_filter"
      solo value="day" flat item-color='red'
      ></v-select>

      <v-spacer v-if="!search_buttom"></v-spacer>
      <v-btn v-if="!search_buttom" icon>
        <v-icon v-on:click="search_buttom=true;search_text=''">mdi-magnify</v-icon>
      </v-btn>
      <v-btn v-if="!search_buttom" icon>
        <v-icon v-on:click="refresh">mdi-refresh</v-icon>
      </v-btn>
      <v-menu
        v-if="!search_buttom"
        bottom
        left
      >
        <template v-slot:activator="{ on, attrs }">
          <v-btn
            icon
            color="yellow"
            v-bind="attrs"
            v-on="on"
          >
            <v-icon>mdi-dots-vertical</v-icon>
          </v-btn>
        </template>

        <v-list
          avatar
          dense
          rounded
        >
        <v-list-item-group v-model="sort" color="primary">
          <v-list-item
            v-for="(item,i) in items"
            :key="i"
            v-on:click="sort_data(item)"
          >
          <v-list-item-avatar >
             <v-icon>{{item.action}}</v-icon>
            </v-list-item-avatar>
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item>
           </v-list-item-group>
        </v-list>
      </v-menu>

      <v-text-field v-if="search_buttom"
      v-model="search_text" hide-details single-line
      label="Search"
      ></v-text-field>
      <v-btn  v-if="search_buttom" icon>
        <v-icon v-on:click="search_buttom=false;search_text=''">mdi-arrow-left-drop-circle</v-icon>
      </v-btn>
      <template  v-slot:extension>
        <v-tabs
          v-model="tabs_selected"
          dense
          center-active
          dark
          @change="toTop"
          background-color="teal accent-4"
        >
        <v-tab class="text-body-2" v-for="i in tabs" :key="i" >
          {{ i }}
        </v-tab>
        </v-tabs>
      </template>

    </v-app-bar>

    <v-main>
    </v-main>
      <v-btn class="footer-button"
        color="teal"
        dark
        small
        v-if="this.data.logged_in_flag"
        center
        fab
        fixed
        v-on:click="add_expense()"
        >
        <v-icon>mdi-plus</v-icon>
      </v-btn>
      <v-btn
      v-scroll="onScroll"
      v-show="fab"
      fab
      dark
      fixed
      small
      bottom
      right
      color="teal"
      @click="toTop"
      >
      <v-icon>keyboard_arrow_up</v-icon>
    </v-btn>
    <v-card dense>
      <v-tabs-items  v-model="tabs_selected">
        <v-tab-item >
          <v-card  tile color="gray" >
            <v-row no-gutters>
              <v-col background-color="red"
              cols="6"
              sm="6"
              >
              <v-list  dense>
                <v-list-item>
                  <v-list-item-content class="pa-0">
                    <v-list-item-title>Expense</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content>
                    <v-list-item-title>Daily Expense</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
                <v-list-item>
                  <v-list-item-content>
                    <v-list-item-title>Top Expense</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list >

            </v-col>
            <v-col background-color="green"
            cols="6"
            sm="6"
                       class="text-right">

                       <v-list  dense>
                         <v-list-item>
                           <v-list-item-content>
                             <v-list-item-title class="red--text">{{total_expense}}</v-list-item-title>
                           </v-list-item-content>
                         </v-list-item>
                         <v-list-item>
                           <v-list-item-content>
                             <v-list-item-title class="blue--text">{{daily_expense}}</v-list-item-title>
                           </v-list-item-content>
                         </v-list-item>
                         <v-list-item>
                           <v-list-item-content>
                             <v-list-item-title class="green--text">{{top_expense}}</v-list-item-title>
                           </v-list-item-content>
                         </v-list-item>
                       </v-list >

                     </v-col>
                 </v-row>
               </v-card>
             </v-tab-item>
             <v-tab-item>
               <v-card flat tile >
                 <v-row no-gutters>
                   <v-col background-color="red"
                   cols="8"
                   sm="6"
                   >
                   <v-list  dense>
                     <v-list-item>
                       <v-list-item-content>
                         <v-list-item-title>Income</v-list-item-title>
                       </v-list-item-content>
                     </v-list-item>
                     <v-list-item>
                       <v-list-item-content>
                         <v-list-item-title>Expense</v-list-item-title>
                       </v-list-item-content>
                     </v-list-item>
                     <v-list-item>
                       <v-list-item-content>
                         <v-list-item-title>Balance</v-list-item-title>
                       </v-list-item-content>
                     </v-list-item>
                   </v-list >

                 </v-col>
                 <v-col background-color="green"
                 cols="4"
                 sm="6"
                            class="text-right">
                            <v-list  dense>
                              <v-list-item>
                                <v-list-item-content>
                                  <v-list-item-title class="green--text">{{cash_flow_income}}</v-list-item-title>
                                </v-list-item-content>
                              </v-list-item>
                              <v-list-item>
                                <v-list-item-content>
                                  <v-list-item-title class="red--text">{{total_expense}}</v-list-item-title>
                                </v-list-item-content>
                              </v-list-item>
                              <v-list-item>
                                <v-list-item-content>
                                  <v-list-item-title class="red--text" v-if="balance.sign">{{balance.bal}}</v-list-item-title>
                                  <v-list-item-title class="green--text" v-else>{{balance.bal*-1}}</v-list-item-title>
                                </v-list-item-content>
                              </v-list-item>
                            </v-list >
                          </v-col>
                      </v-row>
          </v-card>
        </v-tab-item>
        <v-tab-item>

              <v-row  no-gutters>
                <v-col background-color="red"
                cols="5"
                sm="6"
                >
                <v-list  dense>
                  <v-list-item>
                    <v-list-item-content>
                      <v-list-item-title>Expense butget  </v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-list-item>
                    <v-list-item-content>
                      <v-list-item-title>Daily Expense</v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                  <v-list-item>
                    <v-list-item-content>
                      <v-list-item-title>Top Expense</v-list-item-title>
                    </v-list-item-content>
                  </v-list-item>
                </v-list >

              </v-col>
              <v-col
              cols="5"
              sm="6"
                         class="text-right">

                         <v-list  dense>
                           <v-list-item>
                             <v-list-item-content>
                               <v-list-item-title class="red--text">{{total_expense}}</v-list-item-title>
                             </v-list-item-content>
                           </v-list-item>
                           <v-list-item>
                             <v-list-item-content>
                               <v-list-item-title class="blue--text">{{daily_expense}}</v-list-item-title>
                             </v-list-item-content>
                           </v-list-item>
                           <v-list-item>
                             <v-list-item-content>
                               <v-list-item-title class="green--text">{{top_expense}}</v-list-item-title>
                             </v-list-item-content>
                           </v-list-item>
                         </v-list >

                       </v-col>
                       <v-col
                       cols="2"
                       sm="6"
                          >

                             <v-icon x-large color="teal darken-2">mdi-email</v-icon>

                                </v-col>
                   </v-row>


        </v-tab-item>
        <v-tab-item>
          <v-card  tile color="gray" >
            <v-row no-gutters>
              <v-col background-color="red"
              cols="6"
              sm="6"
              >
              <v-list  dense>
                <v-list-item v-if="this.data.account_data[0]">
                  <v-list-item-content >
                    <v-list-item-title>{{this.data.account_data[0].account_name}}</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
                <v-list-item v-if="this.data.account_data[1]">
                  <v-list-item-content >
                    <v-list-item-title>{{this.data.account_data[1].account_name}}</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
                <v-list-item v-if="this.data.account_data[2]">
                  <v-list-item-content >
                    <v-list-item-title>{{this.data.account_data[2].account_name}}</v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list >

            </v-col>
            <v-col background-color="green"
            cols="6"
            sm="6"
                       class="text-right">

                       <v-list  dense>
                         <v-list-item v-if="this.data.account_data[0]">
                           <v-list-item-content >
                             <v-list-item-title>{{this.data.account_data[0].balance}}</v-list-item-title>
                           </v-list-item-content>
                         </v-list-item>
                         <v-list-item v-if="this.data.account_data[1]">
                           <v-list-item-content >
                             <v-list-item-title>{{this.data.account_data[1].balance}}</v-list-item-title>
                           </v-list-item-content>
                         </v-list-item>
                         <v-list-item v-if="this.data.account_data[2]">
                           <v-list-item-content >
                             <v-list-item-title>{{this.data.account_data[2].balance}}</v-list-item-title>
                           </v-list-item-content>
                         </v-list-item>
                       </v-list >

                     </v-col>
                 </v-row>
               </v-card>
        </v-tab-item>
      </v-tabs-items>
      </v-card>
        <!-- Provides the application the proper gutter -->
        <v-container fluid>

      <div v-if="this.data.logged_in_flag" >
        <expense v-bind:data="data" :search="search_text"> </expense>
      </div>
      <div v-else >
        <home_login></home_login>
    </div>
    </v-container>

  </v-card>
</template>

<script>
import { bus } from '../../main.js';
import home_login from '../home/login.vue'
import expense from '../home/expense.vue'

export default {
  data: () => ({
    search_buttom:false,
    search_text:"",
    time_filter_selected:'Day',
    time_filter:['Day','Week','Month','Year'],
    loading: true,
    tabs:["Expense","Cash Flow","Budget","Account"],
    tabs_selected:"Expense",
    fab: false,
    sort:null,
    items: [
       {
         action: 'call_made',
         title: 'Price',
         value: 'amount',
         order : 1,

       },
       {
         action: 'call_received',
         title: 'Price',
         value: 'amount',
         order : -1,


       },
       {
         action: 'call_made',
         value: 'create_on',
         title: 'Date',
         order : 1,

       },
       {
         action: 'call_received',
         value: 'create_on',
         order : -1,
         title: 'Date',
       }
     ]

  }),
  components: {
  "home_login":home_login,
  "expense":expense,
  },
  props: [
    "data"
  ],
  watch: {
     '$props':{
       handler: function (val, oldVal) {
         console.log('[home][watch] new', val)
         this.loading = this.data.loading_flag
         console.log(">>>>>>>>>",this.loading);
         console.log("[home][watch] old",oldVal);
       },
       deep: true
     }
   },
   created() {
    console.log("[home][created] props is :");
    console.log(this.data);
    if(this.data.logged_in_flag ==false){
      console.log("[home][created] login flag not set , login !");
    }
    else{
      console.log("[home][created] login flag is set");
        }

  },
  methods:{
    sort_data: function(item){
      console.log("[home][methods][sort data] over to sort data");
      console.log(item);
      bus.$emit('sort_expense',item);
    },
    refresh: function(){
      console.log("[home][methods][refresh] refreshing data ");
      bus.$emit('refresh');
    },
    call: function(){
      console.log("[home][methods][call_sidebar] call");
    },
    call_sidebar: function(){
      console.log("[home][methods][call_sidebar] Truning on sidebar");
      bus.$emit('sidebar');
    },
    add_expense: function(){
      console.log("[home][methods][add_expense] Switching to add expense ");
      bus.$emit('changecomponent',"expense");
    },
    change_time_filter: function(){
      console.log("[home][methods][change_time_filter] Changing time filter");
      console.log(this.time_filter_selected);
      bus.$emit('changedayfilter',{'time_filter_selected':this.time_filter_selected,'offset':null,'start_day':null,'end_day':null});

    },
    change_tabs: function(){
      console.log("[home][methods][change_tabs] Changing tabs");

      console.log(this.tabs_selected);
    },
    onScroll (e) {
      if (typeof window === 'undefined') return
      const top = window.pageYOffset ||   e.target.scrollTop || 0
      this.fab = top > 20
    },
    toTop () {
      this.$vuetify.goTo(0)
    },

  },
  computed: {
    total_expense: function(){
      var total=0;
      var i;
      for( i in this.data.expense_data){
        if(this.data.expense_data[i].type == "expense"){
          total = total + this.data.expense_data[i].amount
        }
      }
      return total
    },
    daily_expense: function(){
      var total=0;
      var i;
      var cur_date = new Date().toISOString().substr(0, 10);
      for( i in this.data.expense_data){
        if(this.data.expense_data[i].type == "expense"){
          if (this.data.expense_data[i].create_on.includes(cur_date)){
            total = total + this.data.expense_data[i].amount
          }
        }
      }
      return total
    },
    top_expense: function(){
      var top_expense=0;
      var top_object = {"amount":0};
      var i;
      console.log(">>>>>>>>>>>top ");
      for( i in this.data.expense_data){
        if(this.data.expense_data[i].type == "expense"){
          if (this.data.expense_data[i].amount > top_expense){
            top_object = this.data.expense_data[i];
            top_expense = top_object.amount
          }
        }
      }
      var  cat,subcat;
      for (i in this.data.category){
        if(this.data.category[i].id==top_object.category){
          cat = this.data.category[i].category_name
        }
      }
      for (i in this.data.subcategory){
        if(this.data.subcategory[i].id==top_object.sub_category){
          subcat = this.data.subcategory[i].subcategory_name
        }
      }
      console.log(top_expense);
      return cat+ "- "+ subcat +" : "+ top_object.amount
    },
    cash_flow_income: function(){
      var total=0;
      var i;
      for( i in this.data.expense_data){
        if(this.data.expense_data[i].type == "income"){
          total = total + this.data.expense_data[i].amount
        }
      }
      return total
    },
    balance: function(){
      var bal =  this.total_expense - this.cash_flow_income
      var sign = false;
      if (bal > 0){
        sign = true
      }
    return {bal,sign}
    }
    },

}
</script>


<style scoped>
.footer-button{
  position: fixed;
  bottom: 10px;
  left:5%;
}
.toolbar {
  padding: 0px 16px;
    }
.v-slide-group__prev {
  display: none !important;
}
</style>
